.main-navigation__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  text-align: center;
  list-style: none;
  text-transform: uppercase;


  @media (min-width: $tablet-width) {
    width: 440px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    text-align: left;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    width: 479px;
  }
}

.main-navigation__item {
  height: 66px;
  width: 320px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  border-top: 1px solid #e6e6e6;

  &:last-child {
    border-bottom: 1px solid #e6e6e6;
  }

  @media (min-width: $tablet-width) {
    border-top: 0;
    width: auto;

    &:last-child {
      border-bottom: 0;
    }
  }
}

.main-navigation__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 100%;
  font-style: normal;
  font-weight: 500;
  color: #000000;
  text-decoration: none;

  &:hover {
    opacity: 0.6;
    transition: 0.25s ease-in-out;
  }

  &:active {
    opacity: 0.3;
    transition: 0.25s ease-in-out;
  }

  @media (min-width: $tablet-width) {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    padding-bottom: 6px;
    margin-bottom: 2px;
  }

  @media (min-width: $desktop-width) {
    font-size: 20px;
    line-height: 30px;
    padding-bottom: 6px;
    margin-bottom: -2px;
  }
}

.main-navigation__link--main {
  @media (min-width: $desktop-width) {
    color: #ffffff;
  }
}

.main-navigation__link--activ {
  @media (min-width: $tablet-width) {
    margin-top: 2px;
    border-bottom: 2px solid #68b738;
  }

  @media (min-width: $desktop-width) {
    margin-top: 3px;
  }

  &:hover {
    opacity: 1;
  }

  &:active {
    opacity: 1;
  }
}

.main-navigation__link--index-activ {
  @media (min-width: $desktop-width) {
    border-bottom: 2px solid #ffffff;
  }
}

.main-navigation--closed {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
  }
}

.main-navigation--opened {
  display: block;
  position: absolute;
  top: 65px;
  right: 0;
  background-color: #ffffff;

  &::before {
    content: "";
    position: absolute;
    width: 320px;
    height: 2px;
    top: -2px;
    background-color: inherit;
  }

  @media (min-width: $tablet-width) {
    position: static;
  }

  @media (min-width: $desktop-width) {
    background-color: transparent;
  }
}

.main-navigation--no-js {
  display: block;
  position: relative;
  top: 1px;
  right: 20px;
  margin-top: 11px;
}
